@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);

@media only screen and (max-width:600px){
    Sidebar{
        display: none;
    }
}
*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
.sidebar{
    background-color: #fff;
    padding: 20px 40px;
    width: 20%;
    float: left;
    height:auto;
}
.sidebar>img{
    width: 100px;
    height: 100px;
    border-radius: 50%;
    object-fit: cover;
    margin-left: 30px;
    margin-bottom: 20px;
    margin-top: 10px;
}
.sidebar-group{
    height: 100px;
    margin-bottom: 100px;
    padding-left: 20px;
    padding-top: 10px;
}
.new-playlist{
    width: 70%;
    display: flex;
    outline: none;
    margin-top: 200px;
    height: 35px;
    background-color: dodgerblue;
    color: #fff;
    cursor: pointer;
    border: none;
    border-radius: 15px;
    font-size: 13px;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-weight: 540;
    padding: 10px 17px 25px;
}
.new-playlist>.MuiSvgIcon-root{
    font-size: x-large;
    margin-left: -2px;
    margin-right: 5px;
    margin-top: -4px;
}
h2{
    color: rgba(231, 231, 231, 0.966);
    font-family: sans-serif;
    font-size: 17px;
    text-transform: uppercase;
    margin-bottom: 20px;
}

.sidebarOption{
    display: flex;
    margin-bottom: 13px;
    cursor: pointer;
}
.sidebarOption.active{
    background-color: rgba(238, 238, 238, 0.849);
    border-radius: 999px;
    width: 100%;
    margin-left: -20px;
    padding: 10px 20px;
    height: 40px;
}
.sidebarOption>h3{
    margin-left: 15px;
    margin-bottom: 20px;
    font-weight: 300;
    color: rgb(32, 32, 32);
    font-family: sans-serif;
    font-size: 15px;
}
.sidebarOption>.MuiSvgIcon-root{
    font-size: 18px;
    font-weight: normal;
}
.sidebarOption>.MuiSvgIcon-root.active{
    color: rgb(85, 166, 247);
}

.header{
    background-color: #ccc;
    height: 50px;
    width: 100%;
    clear: both;
    display: none;
}
.header .MuiSvgIcon-root:nth-child(1){
    font-size: 40px;
    margin-left: 30px;
    display: none;
}
@media only screen and (max-width:600px){
    .header{
        display: flex;
    }
    .header .MuiSvgIcon-root:nth-child(1){
        display: flex;
    }
}
*{
    box-sizing: border-box;
}
.amazon-sellers{
    width: 40%;
}
.amazon-sellers>h2{
    color: #000;
    font-family: sans-serif;
    font-weight: bold;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    margin-bottom: 27px;
    text-transform: none;
    margin-top: 30px;
    font-size: 25px;
    text-shadow: 0px 0px 1px #000;
}
.amazon-books{
    margin-right: 20px;
    background-color: rgba(236, 236, 236, 0.514);
    border-radius: 10px;
}
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.book-selling{
    width: 100%;
    display: flex;
    margin-bottom: 20px;
    padding-left: 15px;
}
.book-selling:first-child{
    padding:35px 0px 0px 15px;
}
.book-selling:last-child{
    padding-bottom: 20px;
}
.book-selling>img{
    width: 40px !important;
    height:45px !important;
    margin-right: 8px;
    border-radius: 0px !important;
    padding-right: 5px;
    box-shadow: 0px 0px 1px #000;
}
.colon{
    margin-left: 0px !important;
    margin-top: 5px;
}
.position{
    padding-top: 20px;
    padding-right: 10px;
    font-family: sans-serif;
    color: rgba(102, 92, 92, 0.6);
    font-size: 10px !important;
}
.book-selling__info>h3{
    font-family: sans-serif;
    font-weight:bold;
    font-size: 13px;
    margin-top: 5px;
    width: 135px;
}
.book-selling__info>h4{
    font-size: 12px;
    color: rgba(86, 167, 248, 0.726);
    margin-top: 4px;
    font-family: sans-serif;
    font-style: italic;
}
.app-body{
    overflow-x: hidden;
    width: 80%;
    height:auto;   
    float: left;
    padding-top: 50px;
}
.best-books{
    display: flex;
    height: auto;
}
*{
    padding: 0;
    box-sizing: border-box;
    margin: 0;
}
.header-title{
    display: flex;
}
.header-title>h3{
    margin-right: 40%;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-weight: bolder;
    font-size: 27px;
    text-shadow: 0px 0px 2px #000;
}
.buttons{
    display: flex;
}
.buttons button{
    width: 130px;
    height: 35px;
    background-color: rgb(102, 174, 247);
    display: flex;
    color: #fff;
    padding: 10px 20px;
    border: none;
    border-radius: 18px;
    margin-right: 20px;
    cursor: pointer;
    outline: none;
}
.buttons button > .MuiSvgIcon-root{
    margin-top: -5px;
    margin-right: 15px;
}
.navigate-icon{
    background-color: transparent;
    margin-right: 20px;
    display: flex;
    align-items: center;
    width: 40px;
    justify-content:center;
    border-radius: 50%;
    border:2px solid #ccc;
    cursor: pointer;
    outline: none;
}
.navigate-icon>.MuiSvgIcon-root{
    color: rgba(0, 0, 0, 0.733);
}
.selling-books{
    width: 100%;
    display:flex;
}
.book-div{
    background-image: linear-gradient(rgb(116, 115, 115),rgb(212, 209, 209));
    width: 39%;
    height: 230px;
    margin-top: 35px;
    display: flex;
    border-radius: 18px;
    margin-right: 15px;
}
.book-div>img{
    width: 170px;
    height: 230px;
    padding: 15px 15px;
    border-radius: 25px;
}
.book-div__info>h2{
    margin-top: 5px;
    text-transform: capitalize;
    color:#fff;
    font-size: 20px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    margin-left: 5px;
}
.book-div__info>p{
    font-size: 17px;
    font-family: sans-serif;
    color: rgba(255, 255, 255, 0.4);
    margin-top: -20px;
    margin-bottom: 20px;
    margin-left: 5px;
}
.book-buttons{
    display: flex;
}
.book-div__info>.book-buttons>button{
    width: 110px;
    border: none;
    outline: none;
    background-color: #fff;
    margin-right: 10px;
    height: 33px;
    align-items: center;
    justify-content: center;
    border-radius: 15px;
    display: flex;
    padding: 8px 5px;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-weight: 400;
    cursor: pointer;
}
.book-div__info>.book-buttons>button>.MuiSvgIcon-root{
    color: dodgerblue;
    margin-top: -10px;
    padding-top: 10px;
    font-size:32px;
}
.book-div__info>.MuiSvgIcon-root{
    margin-top: 70px;
    font-size: 21px;
    color: #ccc;
}
.book-div__info>.shaded{
    color: rgba(255, 255, 255, 0.897) !important;
}
*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
.book{
    display: flex;
    margin-bottom: 25px;
    width: 32%;
    margin-right: 10px;
}
.book>img{
    height: 180px;
    border-radius: 12px;
    width: 120px;
}
.book-info{
    margin-left: 10px;
}
.book-info>h3{
    font-weight: bold;
    font-family: 'Roboto', sans-serif;
    font-size: 15px;
}
.book-info>p{
    color: rgba(107, 175, 243, 0.733);  
    font-size: 13px;
    font-family:sans-serif;
}
.book-statistics{
    display: flex;
    margin-top: 45px;
}
.book-statistics__plays>p{
    font-family: 'Lato', sans-serif;
    font-size: 13px;
}
.book-statistics>p{
    font-family: 'Lato', sans-serif;;
    font-size: 13px;
}
.book-statistics__plays{
    display: flex;
    margin-top:5px;
}
.book-statistics__plays>.MuiSvgIcon-root{
    color: dodgerblue;
    margin-right:5px;
    font-size: 14px;
}
.book-statistics>.MuiSvgIcon-root{
    color: dodgerblue;
    margin-right:5px;
    font-size: 14px;
}
.book-info>.MuiSvgIcon-root{ 
    font-size: 15px;
    margin-top: 25px;
}
.book-info>.shaded{
    color: rgb(253, 78, 78);
}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.Recommendations{
    height: auto;
}
.Recommendations-books{
    display: flex;
    flex-wrap: wrap;
    width: 100%;
}
.Recommendations>h2{
    color: #000;
    font-family: sans-serif;
    font-weight: bold;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    margin-bottom: 10px;
    text-transform: none;
    margin-top: 30px;
    font-size: 25px;
    text-shadow: 0px 0px 1px #000;
}
